body {
  margin: 0;
  padding: 0;
  background-color: #89674a;
  box-sizing: border-box;
}
.app {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-self: center;
}
.cookie {
  width: fit-content;
  height: fit-content;
  margin-top: 50px;
  animation: infinite 180s cookieAnimation;
  transform-origin: center;
  cursor: pointer;
}
.cookieCounter {
  font-weight: bold;
  font-size: 30px;
  text-align: center;
}
.cookieContainerLeft {
  box-shadow: 5px 5px 10px black;
  padding: 10px 50px;
  background-color: #eee0b1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cookieContainerRightShop {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.cookieContainerRightShop > div {
  width: 300px;
  border: 1px solid black;
  background-color: rgba(255, 255, 255, 0.466);
  padding: 20px 10px;
  cursor: pointer;
  transition: 0.5s;
}
.cookieContainerRightShop > div.available {
  background-color: #2ecc71;
}
.cookieContainerRightShop > div.unavailable:hover {
  background-color: #e74c3c;
}

@keyframes cookieAnimation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.restartButton {
  position: fixed;
  top: 100%;
  left: 100%;
  transform: translate(-100%, -100%);
  height: fit-content;
  background-color: #7f8c8d;
  cursor: pointer;
  transition: 0.2s;
  padding: 10px 20px;
  border: 2px solid black;
}
.restartButton:hover {
  background-color: #e74c3c;
  cursor: pointer;
  box-shadow: 2px 2px 100px black;
  color: white;
}
